<template>
  <div class="StmtDoDialog">
    <div class="stmt-do-dialog-header">
      <UiItem v-bind="innerModel.info">
        <template #text>
          <input
            type="text"
            class="ui-native"
            v-model="innerModel.info.text"
            @input="emitInput"
          />
        </template>

        <template #secondary>
          <input
            type="text"
            v-model="innerModel.info.secondary"
            @input="emitInput"
            style="display:block; width: 100%"
          />
        </template>
      </UiItem>
    </div>

    <div class="stmt-do-dialog-contents">
      <VmExpressionInternal
        v-model="innerModel.do"
        @input="emitInput"
      />
    </div>

    <div class="stmt-assign-editor">
      <input
        type="text"
        class="ui-native"
        placeholder="Resultado en..."
        v-model="innerModel.assign"
        @input="emitInput"
      />
      <a
        href="#"
        @click.prevent="innerModel.assign = null; emitInput()"
      >&times;</a>
    </div>
  </div>
</template>

<script>
import VmExpressionInternal from '../../VmExpressionInternal.vue';
import { UiItem } from '../../../../../ui';

export default {
  name: 'StmtDoDialog',
  components: { VmExpressionInternal, UiItem },

  props: {
    value: {
      required: false,
      default: () => ({}),
    },
  },

  data() {
    return {
      innerModel: {},
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerModel = newValue ? JSON.parse(JSON.stringify(newValue)) : {};
      },
    },
  },

  methods: {
    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerModel)));
    },
  },
};
</script>