<template>
  <div class="StmtDo">
    <UiDataDialog
      v-if="innerModel.do"
      v-model="innerModel"
      @input="emitInput"
      class="stmt-do-dialog"
    >
      <template #trigger>
        <UiItem
          class="ui-clickable"
          v-bind="itemAttrs"
        >
          <template #secondary>
            <div
              v-if="innerModel.assign"
              class="do-then-assign"
            >
              se guarda en <span>{{innerModel.assign}}</span>
            </div>
          </template>

          <template #right>
            <button
              type="button"
              @mousedown.stop="ifify()"
            >?</button>
            <button
              type="button"
              @mousedown.stop="nuke()"
            >&times;</button>
          </template>

        </UiItem>
      </template>
      <template #contents="dialog">
        <StmtDoDialog
          :value="dialog.innerModel"
          @input="dialog.setModel"
        />
      </template>
    </UiDataDialog>
    <StmtDoLauncher
      v-else
      @input="launchDo"
    />

    <div
      class="do-then"
      v-if="innerModel.do"
    >
      <VmExpressionInternal
        v-if="innerModel.then"
        v-model="innerModel.then"
        @input="emitInput"
      />
      <StmtDoLauncher
        v-else
        @input="launchThen"
      />
    </div>
  </div>
</template>

<script>
import StmtDoDialog from '../../statements/do/StmtDoDialog.vue';
import VmExpressionInternal from '../../VmExpressionInternal.vue';
import StmtDoLauncher from '../../statements/do/StmtDoLauncher.vue';
import { UiItem, UiDataDialog } from '../../../../../ui';

export default {
  name: 'StmtDo',
  components: {
    StmtDoDialog,
    UiItem,
    UiDataDialog,
    VmExpressionInternal,
    StmtDoLauncher,
  },

  props: {
    value: {
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerModel: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        let clone = newValue ? JSON.parse(JSON.stringify(newValue)) : newValue;
        this.innerModel = Object.assign(
          {
            do: null,
            then: null,
            assign: null,
          },
          clone
        );
      },
    },
  },

  computed: {
    itemAttrs() {
      return Object.assign(
        {
          icon: 'mdi:variable',
          text: 'DO',
        },
        this.innerModel.info
      );
    },
  },

  methods: {
    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerModel)));
    },

    ifify() {
      this.innerModel = {
        if: { and: [] },
        then: this.innerModel,
        else: { do: null },
      };

      this.emitInput();
    },

    nuke() {
      if (!confirm('Eliminar esta accion ?')) {
        return;
      }

      this.innerModel = this.innerModel.then || { do: null };
      this.emitInput();
    },

    launchDo(incoming) {
      this.innerModel = incoming;
      this.emitInput();
    },

    launchThen(incoming) {
      this.innerModel.then = incoming;
      this.emitInput();
    },
  },
};
</script>

<style lang="scss">
.stmt-do-dialog {
  /// !!! sobreescribir clase interna
  .ui-dialog-content {
    padding: 0 !important;
  }

  .stmt-do-dialog-contents {
    padding: var(--ui-padding);
  }
}

.stmt-assign-editor {
  padding: var(--ui-padding);
  text-align: right;
  background-color: #f3f3f355;
}

.do-then-assign {
  span {
    background-color: var(--ui-color-primary);
    color: #fff;
    // font-weight: bold;
    // font-family: var(--ui-font-secondary);
    font-size: 13px;
    border: 0;
    border-radius: var(--ui-radius);
    padding: 1px 7px;
    display: inline-block;
  }
}
</style>