<template>
  <div class="StmtDoLauncher">
    <UiPopover placement="bottom-start">
      <template #trigger>
        <div class="launcher-trigger">
          <button type="button">+</button>
        </div>
      </template>

      <template #contents="{close}">
        <div class="launcher-popover-contents">
          <div class="launcher-statements">
            <UiItem
              class="ui-clickable"
              @click="close(); launchIf()"
              icon="mdi:help-rhombus"
              text="IF"
            />
          </div>

          <div class="launcher-functions">
            <UiItem
              v-for="fn in functions"
              :key="fn.name"
              v-bind="fn"
              class="ui-clickable"
              @click="close(); launchFunction(fn)"
            />
          </div>
        </div>
      </template>
    </UiPopover>

    <UiDialog
      :open="!!staging.do"
      @accept="dialogAccept"
      @cancel="dialogCancel"
    >
      <div class="launcher-staging">
        <StmtDoDialog v-model="staging" />
      </div>
    </UiDialog>
  </div>

</template>

<script>
import definitions from '../../definitions/index.js';

import StmtDoDialog from './StmtDoDialog.vue';
import { UiItem, UiDialog, UiPopover } from '../../../../../ui';

export default {
  name: 'StmtDoLauncher',
  components: { StmtDoDialog, UiItem, UiDialog, UiPopover },

  data() {
    return {
      staging: {
        do: null,
        item: {},
      },
    };
  },

  computed: {
    functions() {
      let retval = [];
      for (let fxName in definitions.functions) {
        retval.push(
          Object.assign({ name: fxName }, definitions.functions[fxName])
        );
      }
      return retval;
    },

    operators() {
      let retval = [];
      for (let opName in definitions.operators) {
        retval.push(
          Object.assign({ name: opName }, definitions.operators[opName])
        );
      }
      return retval;
    },
  },

  methods: {
    dialogAccept() {
      this.$emit('input', this.staging);
      this.staging.do = null;
      this.staging.assign = null;
    },

    dialogCancel() {
      this.staging.do = null;
      this.staging.assign = null;
    },

    launchFunction(fn) {
      this.staging = {
        info: {
          icon: fn.icon,
          text: fn.text,
          secondary: fn.secondary,
        },
        do: {
          call: fn.name,
          args: null,
        },
        assign: null,
      };
      // this.$emit('input', retval);
    },

    launchIf() {
      // this.$emit('input', {
      //   if: { and: [] },
      //   then: { do: null },
      //   else: { do: null },
      // });

      this.$emit('input', {
        info: {
          icon: 'mdi:help-rhombus',
          text: 'If ...',
        },
        do: {
          if: { and: [] },
          then: { do: null },
          else: { do: null },
        },
        then: null,
        assign: null,
      });
    },

    // launchOperator(op) {
    //   this.$emit('input', {
    //     op: op.name,
    //     field: null,
    //     args: null,
    //   });
    // },
  },
};
</script>

<style lang="scss">
.StmtDoLauncher {
  .launcher-trigger {
    margin: 0 var(--ui-padding);
  }

  .launcher-popover-contents {
    width: 480px;
    max-height: 500px;
    overflow: auto;
  }
}
</style>